* {
  box-sizing: border-box; }

main {
  display: block; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace, monospace;
  font-size: 1em; }

a {
  background-color: transparent; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b,
strong {
  font-weight: bolder; }

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: 0.35em 0.75em 0.625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details {
  display: block; }

summary {
  display: list-item; }

template {
  display: none; }

[hidden] {
  display: none; }

.page {
  font-size: 10px;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  height: 100%; }
  .page__body {
    margin: 0;
    height: 100%;
    font-family: "Poppins", "CormorantGaramond", "Arial", sans-serif; }
  .page__wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100%; }

.main {
  flex: 1 0 auto; }

.link {
  text-decoration: none;
  cursor: pointer; }
  .link:hover {
    opacity: 0.4; }

.overflow-hidden {
  overflow: hidden; }

.container {
  max-width: 1215px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 10px; }
  @media (min-width: 767px) {
    .container {
      padding: 0 20px; } }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"] .visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

input::placeholder {
  font-family: "Poppins";
  font-size: 1.4rem;
  color: #9B9997;
  font-weight: 500; }

@font-face {
  font-family: "CormorantGaramond";
  src: url("../fonts/cormorant/CormorantGaramond-Bold.woff") format("woff"), url("../fonts/cormorant/CormorantGaramond-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap; }

@font-face {
  font-family: "CormorantGaramond";
  src: url("../fonts/cormorant/CormorantGaramond-SemiBold.woff") format("woff"), url("../fonts/cormorant/CormorantGaramond-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap; }

@font-face {
  font-family: "CormorantGaramond-MediumItalic";
  src: url("../fonts/cormorant/CormorantGaramond-MediumItalic.woff") format("woff"), url("../fonts/cormorant/CormorantGaramond-MediumItalic.woff2") format("woff2");
  font-weight: 500;
  font-display: swap; }

@font-face {
  font-family: "CormorantGaramond-Italic";
  src: url("../fonts/cormorant/CormorantGaramond-Italic.woff") format("woff"), url("../fonts/cormorant/CormorantGaramond-Italic.woff2") format("woff2");
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins/Poppins-Medium.woff") format("woff"), url("../fonts/poppins/Poppins-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap; }

@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins/Poppins-Regular.woff") format("woff"), url("../fonts/poppins/Poppins-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap; }
